// ========================= [ Header styles ] =========================
@import "_hamburgers.scss";

.hamburger-wrap {
  position: relative;
  right: -5px;
  z-index: 6;
}


.header {
  z-index: 100;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;

  @include media-breakpoint-down(sm) {
    padding-top: 14px;
    padding-bottom: 14px;

    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;

      background: darken(#a8cadf, 60);
      opacity: 0;

      transition: none;
      pointer-events: none;
    }

    &.active {
      &:before {
        z-index: 6;
        opacity: 1;
        pointer-events: auto;
        transition: opacity .3s ease .3s;

      }
    }
  }


  // .header__body

  &__col {
    @include media-breakpoint-up(md) {
      width: 16%;
    }

    @include media-breakpoint-up(lg) {
      width: per(226px, 1800px);
    }

    &_logo {
      @include media-breakpoint-down(sm) {
        flex-grow: 1;
      }
    }
  }

  &__body {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    // min-height: 47px;
    column-gap: 20px;

    @include media-breakpoint-down(lg) {
      column-gap: responsive(lg, 20px);
    }

    @include media-breakpoint-down(md) {
      column-gap: 5px;
    }

    @include media-breakpoint-down(sm) {
      min-height: 50px;
    }

  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 6;
    flex-shrink: 0;
    max-width: 226px;

    @include media-breakpoint-down(sm) {
      max-width: 100px;

    }

    a {
      display: block;
    }

    img {
      display: block;
    }

  }

  // .header__navbar

  &__navbar {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 45px;

    position: relative;
    z-index: 5;

    @include media-breakpoint-down(lg) {
      column-gap: responsive(lg, 45px);
    }

    @include media-breakpoint-down(md) {
      column-gap: responsive(md, 45px);
    }
  }

  // .header__menu-wrap

  &__menu-wrap {
    @include media-breakpoint-down(sm) {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: darken(#a8cadf, 30);

      padding: 100px 10px 30px 10px;
      transition: 0.3s ease 0s;
      transform: translateX(-100%);
      overflow: auto;


      &.active {
        transform: translateX(0);
      }
    }
  }

  // .header__menu

  &__menu {}

  // .header__list

  &__list {
    display: flex;
    align-items: center;
    position: relative;
    flex-wrap: wrap;
    z-index: 3;

    @include media-breakpoint-down(sm) {
      display: block;
      text-align: center;
    }

    &>li {
      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }

    li {
      &:not(:last-child) {
        @include fz(55px, xl, lg, md, sm, null, 'margin-right');

        @include media-breakpoint-up(xxl) {
          margin-right: 55/16+em;
        }

        @include media-breakpoint-down(sm) {
          margin-right: 0 !important;
        }
      }

      a {
        color: #FFF;
        @include fz(17px, null, 15px, 14px, 18px, null, 'font-size');
        font-weight: 700;
        line-height: 100%;

        position: relative;
        font-size: 17px;
        font-weight: 400;
        line-height: glh(16, 30);

        @include media-breakpoint-down(sm) {
          color: #fff;
          font-weight: 600;
        }

        .header_fixed & {
          color: #fff;
        }

        &:before {
          content: '';
          position: absolute;
          width: 100%;
          height: 2px;
          bottom: 0;
          background: var(--orange-color);
          transition: .3s;
          border-radius: 5px;
          transform: scale(0);



          @include media-breakpoint-down(sm) {
            display: none;
          }
        }

        &:hover::before {
          transform: scale(1);
        }

        &:hover {
          color: var(--orange-color);
        }

        @include media-breakpoint-down(sm) {
          display: block;
        }
      }

      &.menu-item-has-children {
        position: relative;

        &:hover {
          &>a {
            &:after {
              filter: none !important;

              @include media-breakpoint-up(md) {
                transform: translate(0, -24%) rotate(180deg);
              }
            }
          }

          .submenu {
            opacity: 1;
            pointer-events: auto;
          }
        }

        &>a {
          padding-right: 17px;

          @include media-breakpoint-down(sm) {
            padding-left: 17px;

            &.active {
              &:after {
                transform: translate(0, -24%) rotate(180deg);
              }
            }
          }

          &:after {
            content: '';
            display: block;
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate(0, -24%);

            background: url("../img/header/select-arrow.svg") center top no-repeat;
            width: 8px;
            height: 5px;
            filter: brightness(0) invert(1);
            transition: transform .2s ease;
          }
        }
      }
    }
  }

  .submenu {
    border-radius: 4px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .25);
    background: lighten($color: #172f3e, $amount: 20);

    @include media-breakpoint-up(md) {
      display: block !important;
      transition: opacity .3s ease;
      position: absolute;
      top: 99.9%;
      left: 0;
      min-width: 220px;
      width: 100%;
      padding: 0 !important;

      opacity: 0;
      pointer-events: none;

    }

    @include media-breakpoint-down(sm) {
      margin-top: 10px;
      display: none;
    }

    li {
      margin-right: 0 !important;

      &:first-child {
        padding-top: 12px;
      }

      &:last-child {
        padding-bottom: 12px;
      }

      a {
        padding: 5px 15px;

        display: block;
        font-size: 14px;

        &:before {
          display: none !important;
        }

        transition: background-color .3s ease;

        &:hover {
          background: lighten($color: #172f3e, $amount: 30);
        }
      }
    }
  }


  // .header__language

  &__language {}
}

.container {}


.lang {
  display: inline-block;
  vertical-align: top;
  position: relative;
  z-index: 6;
  // .lang__toggle

  &__toggle {
    @include fz(40px, null, null, 30px, null, null, 'width');
    @include fz(40px, null, null, 30px, null, null, 'height');

    border-radius: 50%;

    background-color: #FFF;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    color: #4D4D4D;

    font-size: 16px;
    font-weight: 700;
    line-height: 100%;

    @include media-breakpoint-down(md) {
      font-size: 14px;
    }
  }
}




