// .home

.home {
  position: relative;
  background-position: center center !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
  position: relative;
  z-index: 5;

  &__decor {
    position: absolute;
    right: 0px;
    top: 0;
    height: 100%;
    z-index: -1;

    @media (max-width: 360px) {
      right: -40px;
      top: 20px;
      height: 100%;
    }


    img {
      height: 100%;
    }
  }

  @include fz(640px, xl, lg, md, sm, null, 'padding-top');

  @media screen and (min-width: 1400px)and(max-width: 1920px) {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: stretch;
    justify-content: flex-end;
  }

  @include media-breakpoint-up(xxl) {
    padding-top: 480/16+em;
  }

  @include fz(120px, xl, lg, md, sm, null, 'padding-bottom');

  @include media-breakpoint-up(xxl) {
    padding-bottom: 120/16+em;
  }

  &__content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: flex-start;

    @include media-breakpoint-down(sm) {
      display: block;
      text-align: center;
    }
  }

  &__content-p {
    @include fz(18px, xl, lg, md, xl, null, 'margin-bottom');

    @include media-breakpoint-up(xxl) {
      margin-bottom: 18/16+em;
    }
  }

  &__content-left {
    width: 50%;

    @include media-breakpoint-down(sm) {
      width: 100%;

    }

    p {
      color: #FFF;
      @include fz(24px, xl, lg, 12px, 16px, 14px, 'font-size');

      @include media-breakpoint-up(xxl) {
        font-size: 24/16+em;
      }

      font-weight: 700;
      line-height: 99.958%;
    }
  }

  &__content-right {
    @include fz(52px, xl, lg, md, lg, null, 'padding-top');

    @include media-breakpoint-up(md) {
      text-align: right;
    }

    @include media-breakpoint-up(xxl) {
      padding-top: 52/16+em;
    }

    width: per(490px, 1800px);
    margin-left: auto;

    @include media-breakpoint-down(sm) {
      width: 100%;

    }

    p {
      color: #FFF;

      @include fz(42px, xl, lg, md, 20px, null, 'font-size');

      @include media-breakpoint-up(xxl) {
        font-size: 42/16+em;
      }

      font-weight: 700;
      line-height: 110%;
      margin-left: auto;

      span {
        color: #FFC711;
      }

      br {
        @include media-breakpoint-down(sm) {
          display: none;
        }
      }
    }
  }

  &__content-right-btn {
    @include fz(34px, xl, lg, md, xl, null, 'margin-top');

    @include media-breakpoint-up(xxl) {
      margin-top: 34/16+em;
    }
  }
}


// project section

.projects {
  overflow: hidden;

  @include fz(160px, xl, lg, md, lg, 40px, 'padding-top');

  @include media-breakpoint-up(xxl) {
    padding-top: 160/16+em;
  }

  &__title {
    position: relative;
    margin-bottom: -40px;
    z-index: 5;

    @include fz(126px, xl, lg, md, lg, 0px, 'padding-left');

    @include media-breakpoint-up(xxl) {
      padding-left: 126/16+em;
    }

    @include media-breakpoint-down(lg) {
      margin-bottom: 40px;
    }

    @include media-breakpoint-down(md) {
      margin-bottom: 20px;
    }
  }

  // .project__swiper

  &__swiper {
    overflow: visible;

    .swiper-slide {
      // width: per(665px, 1920px);
      width: 36.92%;

      @include media-breakpoint-down(md) {
        width: 50%;
      }

      @include media-breakpoint-down(sm) {
        width: 80%;
      }

      @include media-breakpoint-down(xs) {
        width: 90%;
      }
    }
  }
}



.card {
  width: 100%;
  position: relative;

  // .card__item

  &__item {}

  &__item-img {
    content: '';
    display: block;
    padding-top: per(698px, 665px);

    img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .card__content

  &__content {
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    @include fz(60px, xl, lg, md, lg, 15px, 'padding');

    @include media-breakpoint-up(xxl) {
      padding: 60/16+em;
    }
  }

  // .card__content-text

  &__content-text {
    p {
      font-family: $font2;
      font-weight: 700;
      @include fz(48px, xl, lg, md, lg, null, 'font-size');
      line-height: glh(48, 64);

      @include media-breakpoint-up(xxl) {
        font-size: 48/16+em;

      }
    }
  }

  &__content-btn {
    flex-shrink: 0;
  }

  .card__content-btn.icon-arrow-right {
    background-color: var(--orange-color);
    @include fz(70px, xl, lg, md, lg, 35px, 'width');
    @include fz(70px, xl, lg, md, lg, 35px, 'height');
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .3s ease;

    @include media-breakpoint-up(xxl) {
      width: 70/16+em;
      height: 70/16+em;
    }

    &::before {
      content: '\e901';
      color: #272727;
      font-size: 24px;
      transition: .3s ease;


      @include media-breakpoint-down(lg) {
        font-size: 16px;
      }
    }

    &:hover {
      background: rgb(255, 184, 0.8);

      &::before {
        color: #fff;
      }
    }
  }
}

.swiper-navs {
  margin-top: 20px;

  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;

  column-gap: 20px;

  .prev,
  .next {
    cursor: pointer;
    @include fz(70px, 50px, lg, 50px, lg, 35px, 'width');
    @include fz(70px, 50px, lg, 50px, lg, 35px, 'height');
  }
}


// descr-section

.descr {
  @include fz(40px, xl, lg, md, lg, null, 'margin-top');

  @include media-breakpoint-up(xxl) {
    margin-top: 40/16+em;
  }

  position: relative;
  margin-bottom: -10%;

  // .descr__decor

  &__decor {
    position: relative;

    img {
      position: absolute;
      width: 100%;
      height: auto;
      top: 0;
      left: 0;
      z-index: 5;
    }
  }

  // .descr__inner

  &__inner {
    position: relative;
    z-index: 10;
    max-width: 52.546448%;
    margin: 0 auto;

    @include media-breakpoint-down(sm) {
      max-width: 100%;
    }

    P {
      font-size: 42px;
      font-weight: 700;
      line-height: glh(40, 42);
      color: #2c2c2c;

      @include fz(42px, xl, lg, md, 30px, 18px, 'font-size');

      @include media-breakpoint-up(xxl) {
        font-size: 42/16+em;
      }

      span {
        color: #fff;
      }
    }

  }


  &__title {

    position: relative;
    z-index: 5;

    @media (min-width: 1400px) {
      font-size: calc(0.0083333333 * 100vw + 0px);
    }

    h4 {
      font-size: 140px;
      font-weight: 700;
      line-height: glh(140, 154);
      position: relative;
      z-index: 5;
      text-align: center;


      @include fz(148px, xl, lg, md, 50px, 36px, 'padding-top');

      @include fz(140px, xl, lg, md, 50px, 36px, 'font-size');

      @include media-breakpoint-up(xxl) {
        font-size: 140/16+em;
        padding-top: 148/16/(140/16)+em;
      }



    }

  }


}