@keyframes fixedHeader {
  from {
    top: -100%
  }

  to {
    top: 0
  }
}

.header {

  &_fixed {
    min-height: 40px;

    position: fixed;
    top: -100%;
    left: 0;
    right: 0;

    // background-color: #4a4a4ade;
    // background-color: darken(#a8cadf, 30);
    background-color: rgba(#4084ae, .94);


    animation-name: fixedHeader;
    animation-duration: .4s;
    animation-fill-mode: both;

    z-index: 101;

    padding-top: 10px !important;
    padding-bottom: 10px !important;
  }
}