@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon/icomoon.eot?ddjf2p');
  src: url('../fonts/icomoon/icomoon.eot?ddjf2p#iefix') format('embedded-opentype'),
    url('../fonts/icomoon/icomoon.ttf?ddjf2p') format('truetype'),
    url('../fonts/icomoon/icomoon.woff?ddjf2p') format('woff'),
    url('../fonts/icomoon/icomoon.svg?ddjf2p#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow-left:before {
  content: "\e900";
}

.icon-arrow-right:before {
  content: "\e901";
}