// commons

h1 {
  // @include fz(150px, lg, md, 28px, 28px, 'font-size');
  // text-align: left;
  font-weight: 700;
  line-height: 100%;
  @include fz(150px, xl, lg, md, 50px, 36px, 'font-size');

  @include media-breakpoint-up(xxl) {
    font-size: 150/16+em;
  }
}

h2 {
  font-weight: 700;
  line-height: 1.2;

  @include fz(220px, xl, lg, md, 50px, 36px, 'font-size');

  @include media-breakpoint-up(xxl) {
    font-size: 220/16+em;
  }
}

h3 {
  font-family: $font2;
  font-size: 12px;
  font-weight: 700;
  line-height: glh(12, 24);
  text-transform: uppercase;
}